/* eslint-disable */
import react from 'react'
import {
  SwipeCell,
  Button,
  CellGroup,
  Cell,
  ISwiperCellInstance,
} from '@antmjs/vantui'

export default function Demo() {
  const it = react.useRef<ISwiperCellInstance>()
  const closeAction = () => {
    setTimeout(() => {
      it.current?.close()
    }, 2000)
  }

  return (
    <SwipeCell
      ref={it}
      rightWidth={75}
      leftWidth={75}
      asyncClose
      onClose={closeAction}
      renderLeft={<Button></Button>}
      renderRight={<Button></Button>}
    >
      <CellGroup>
        <Cell title="" value="" />
      </CellGroup>
    </SwipeCell>
  )
}
