/* eslint-disable */
import react from 'react'
import {
  SwipeCell,
  Button,
  CellGroup,
  Cell,
  ISwiperCellInstance,
} from '@antmjs/vantui'

export default function Demo() {
  const it = react.useRef<ISwiperCellInstance>()

  react.useEffect(() => {
    it.current?.open('right')
  }, [])

  return (
    <SwipeCell
      ref={it}
      rightWidth={75}
      leftWidth={75}
      asyncClose
      renderLeft={<Button></Button>}
      renderRight={<Button></Button>}
    >
      <CellGroup>
        <Cell title="" value="" />
      </CellGroup>
    </SwipeCell>
  )
}
